<template>
    <div class="page">
        <div class="cont">
            <h3>人像面</h3>
            <p>点击请上传身份证人像面</p>
            <div style="position: relative">
                <img @click="afterRead1" src="./img/live_ID_front@2x.png" alt="">
                <div v-if="idPicture1"
                     style="position: absolute;left: 0;top: 0;text-align: center;width: 100%;background: #C1E3DB;border-radius: 0.2rem">
                    <img style="width: 5.42rem;height: 4.1rem;margin: auto;display: block" :src="idPicture1" alt="">
                    <img @click="idPicture1=null"
                         style="position: absolute;right: 0.3rem;width: 0.6rem;height: 0.6rem;top: 0.2rem;"
                         src="./img/live_photo_close@2x.png" alt="">
                </div>
            </div>
            <h3>国徽面</h3>
            <p>点击请上传身份证国徽面</p>
            <div style="position: relative">
                <img style="position: absolute;left: 0;top: 0;" @click="afterRead2" src="./img/live_ID_back@2x.png"
                     alt="">
                <div v-if="idPicture2"
                     style="position: absolute;left: 0;top: 0;text-align: center;width: 100%;background: #C1E3DB;border-radius: 0.2rem">
                    <img style="width: 5.42rem;height: 4.1rem;margin: auto;display: block" :src="idPicture2" alt="">
                    <img @click="idPicture2=null"
                         style="position: absolute;right: 0.3rem;width: 0.6rem;height: 0.6rem;top: 0.2rem;"
                         src="./img/live_photo_close@2x.png" alt="">
                </div>
            </div>
            <div class="footer">
                <button @click="protocolUpdate()">提交身份证信息</button>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "idPage",
        data() {
            return {
                idPicture1: null,
                idPicture2: null,
            }
        },
        methods: {
            protocolUpdate(){
                if(!this.idPicture1){
                    this.$toast('请上传身份证正面')
                    return false
                }
                if(!this.idPicture2){
                    this.$toast('请上传身份证反面')
                    return false
                }
                this.$api.protocolUpdate({
                    idPicture1: this.idPicture1,
                    idPicture2: this.idPicture2
                }).then(()=>{
                    history.back();
                })
            },
            afterRead1() {
                this.$bs.upImage({type: '1'}).then((e) => {
                    console.log(e);
                    if (e.status == '1') {
                        this.idPicture1 = e.data.data
                    } else if (e.msg) {
                        this.$toast(e.msg)
                    }

                })
            },
            afterRead2() {
                this.$bs.upImage({type: '1'}).then((e) => {
                    console.log(e);
                    if (e.status == '1') {
                        this.idPicture2 = e.data.data
                    } else if (e.msg) {
                        this.$toast(e.msg)
                    }

                })
            },
        }
    }
</script>

